.queue {
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    display: grid;

}

.queueItem {
    border-top: 1px solid #e0e0e0;
    padding: 6px;
    overflow: auto;
    margin: 6px;
    display: flex;
    justify-content: space-between;
    text-decoration: none;
}

.queueItem p{
    overflow: fragments;
    flex-basis: 90%;
}

.queueItem span{
    flex-basis: 10%;
    margin: auto;
}

.queueItem:first-child {
    border-top: none;
    background-color: #ff8442;
    margin: 6px -20px;
    border-radius: 5px;
    color: white;
    font-weight: normal;
    font-size: 1.125em;
}


.queueContainer{
    border: 1px solid #e0e0e0;
    border-radius: 5px;
    padding: 6px;
    box-shadow: 0px 1px 2px rgba(0,0,0,0.2);
    margin: 6px;
    text-decoration: none;
}

.queueContainer h3 {
    text-align: center;
}
.empty {
    text-align: center;
    color: #e0e0e0;
    font-size: 1.5em;
    font-weight: lighter;
    line-height: 48px;
}
