.songPanel{
    background: #ffffff;
    padding: 6px;
    margin: 6px;
    display: flex;
    flex-direction: column;
    border: 1px solid #e0e0e0;
    border-radius: 5px;
    box-shadow: 0px 1px 2px rgba(0,0,0,0.2);
}

.audioDuration{
    width: 100%;
    border-radius: 5px;
}

.inputUrl{
    line-height: 2;
    border-radius: 5px;
    border: 1px solid #e0e0e0;
    padding-left: 5px;
    margin: 8px;
    font-size: 14px;
}
.inputUrl:focus-visible {
    border-color: #ff8442;
    outline: inherit;
}

.controls {
    width: 30px;
}
.audioStream{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
}

.songContainer {
    margin: 6px;
}

.audio {
    margin: 11px 0px;
    height: 2px;
    background-color: #e0e0e0;
    flex-grow: 8.5;
}
.timer {
    text-align: center;
    flex-grow: 1.5;
}
.audioPlayer {
    padding: 6px;
    display: flex;
    flex-direction: column;
    border: 1px solid #e0e0e0;
    border-radius: 5px;
    box-shadow: 0px 1px 2px rgba(0,0,0,0.2);
}


